
export const ADD = 'votes@add';
export const REMOVE = 'votes@remove';

export function add(type) {
    return {
        type: ADD,
        payload: { type },
    };
}

export function remove(type) {
    return {
        type: REMOVE,
        payload: { type },
    };
}



