
import { ADD, REMOVE } from 'actions/votes-actions';

export const initialState = {
    normal: 0,
    veggie: 0,
    vegan: 0,
    gfree: 0,
    nopizza: 0,
};

export function votesReducer(state = initialState, action) {
    var { type, payload } = action;
    switch (type) {
        case ADD:       return addVote(state, payload);
        case REMOVE:    return removeVote(state, payload);
        default:        return state;
    }
}

export function addVote(state, payload) {
    var { type } = payload;
    return {
        ...state,
        [type]: state[type] + 1,
    };
}

export function removeVote(state, payload) {
    var { type } = payload;
    var val = state[type];

    if (val > 0) {
        val -= 1;
    }

    return {
        ...state,
        [type]: val,
    };
}



